import React, { useEffect, useRef } from "react";
import anime from "animejs";

const WindParticles = () => {
  const windRef = useRef(null);

  useEffect(() => {
    const particles = windRef.current.children;

    const animations = Array.from(particles).map((particle, index) => {
      return anime({
        targets: particle,
        translateX: [{ value: -300, duration: anime.random(1000, 2000) }],
        translateY: [
          { value: anime.random(-10, 10), duration: anime.random(500, 1000) },
        ],
        scale: [
          { value: 0, duration: 0 },
          { value: 1, duration: 200 },
          { value: 0, duration: 800 },
        ],
        opacity: [
          { value: 0, duration: 0 },
          { value: 0.8, duration: 200 },
          { value: 0, duration: 800 },
        ],
        delay: anime.random(0, 1000),
        loop: true,
        easing: "easeInOutQuad",
      });
    });

    return () => animations.forEach((anim) => anim.pause());
  }, []);

  return (
    <div
      ref={windRef}
      style={{
        position: "absolute",
        inset: 0,
        overflow: "hidden",
        pointerEvents: "none",
        borderRadius: "12px",
      }}
    >
      {[...Array(20)].map((_, i) => (
        <div
          key={i}
          style={{
            position: "absolute",
            right: -10,
            top: `${anime.random(0, 100)}%`,
            width: anime.random(20, 60),
            height: 2,
            background:
              "linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.8) 50%, rgba(255,255,255,0) 100%)",
            filter: "blur(1px)",
            transform: "translateX(0)",
            opacity: 0,
          }}
        />
      ))}
    </div>
  );
};

export default function WindToast({
  message,
  style = {},
  description,
  action,
  onClose,
  ...rest
}) {
  const toastRef = useRef(null);
  const contentRef = useRef(null);
  const progressRef = useRef(null);

  useEffect(() => {
    // Entrance animation
    anime({
      targets: toastRef.current,
      translateX: [100, 0],
      opacity: [0, 1],
      duration: 800,
      easing: "easeOutExpo",
    });

    // Content swaying animation
    anime({
      targets: contentRef.current,
      translateX: [-5, 0],
      duration: 1500,
      direction: "alternate",
      loop: true,
      easing: "easeInOutSine",
    });

    // Progress bar
    if (progressRef.current) {
      anime({
        targets: progressRef.current,
        width: ["0%", "100%"],
        duration: rest.duration || 5000,
        easing: "linear",
      });
    }
  }, []);

  return (
    <div
      ref={toastRef}
      style={{
        display: "flex",
        alignItems: "center",
        background:
          "linear-gradient(135deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.05) 100%)",
        backdropFilter: "blur(10px)",
        color: "#FFFFFF",
        boxShadow: "0 8px 32px rgba(31, 38, 135, 0.15)",
        border: "1px solid rgba(255, 255, 255, 0.1)",
        width: "320px",
        minHeight: "60px",
        borderRadius: "12px",
        padding: "16px 24px",
        margin: "8px",
        fontSize: "16px",
        position: "relative",
        overflow: "hidden",
        ...style,
      }}
      {...rest}
    >
      <WindParticles />
      <div
        style={{
          position: "absolute",
          bottom: 0,
          left: 0,
          right: 0,
          height: "3px",
          background: "rgba(255, 255, 255, 0.1)",
        }}
      >
        <div
          ref={progressRef}
          style={{
            height: "100%",
            background: "rgba(255, 255, 255, 0.3)",
            width: "0%",
          }}
        />
      </div>
      <div
        ref={contentRef}
        style={{
          flex: 1,
          position: "relative",
          zIndex: 1,
        }}
      >
        <div style={{ fontWeight: 500 }}>{message}</div>
        {description && (
          <div
            style={{
              fontSize: "14px",
              marginTop: "4px",
              opacity: 0.8,
            }}
          >
            {description}
          </div>
        )}
      </div>
      {action && (
        <button
          onClick={action.onClick}
          style={{
            background: "rgba(255,255,255,0.1)",
            border: "1px solid rgba(255,255,255,0.2)",
            color: "#fff",
            padding: "8px 16px",
            marginLeft: "16px",
            borderRadius: "6px",
            cursor: "pointer",
            transition: "all 0.3s ease",
            position: "relative",
            zIndex: 1,
          }}
          onMouseEnter={(e) => {
            e.target.style.background = "rgba(255,255,255,0.2)";
            e.target.style.transform = "translateX(-2px)";
          }}
          onMouseLeave={(e) => {
            e.target.style.background = "rgba(255,255,255,0.1)";
            e.target.style.transform = "translateX(0)";
          }}
        >
          {action.label}
        </button>
      )}
      <button
        onClick={onClose}
        style={{
          background: "transparent",
          border: "none",
          color: "#fff",
          marginLeft: "12px",
          cursor: "pointer",
          opacity: 0.7,
          transition: "opacity 0.2s",
          position: "relative",
          zIndex: 1,
        }}
        onMouseEnter={(e) => (e.target.style.opacity = "1")}
        onMouseLeave={(e) => (e.target.style.opacity = "0.7")}
      >
        âœ•
      </button>
    </div>
  );
}
