import React, { useEffect } from "react";
import { useToastStore } from "./store";
import { POSITIONS, VARIANTS, getVariantConfig } from "./toast";
import anime from "animejs";

const positionStyles = {
  "top-left": { top: 20, left: 20 },
  "top-right": { top: 20, right: 20 },
  "top-center": { top: 20, left: "50%", transform: "translateX(-50%)" },
  "bottom-left": { bottom: 20, left: 20 },
  "bottom-right": { bottom: 20, right: 20 },
  "bottom-center": { bottom: 20, left: "50%", transform: "translateX(-50%)" },
};

export function ToastContainer({ position = POSITIONS.TOP_RIGHT }) {
  const { toasts, removeToast } = useToastStore();

  useEffect(() => {
    toasts.forEach((toast) => {
      const config = getVariantConfig(toast.variant);
      if (config.autoClose && !toast.isPaused) {
        const timer = setTimeout(() => {
          removeToast(toast.id);
        }, config.duration || 5000);
        return () => clearTimeout(timer);
      }
    });
  }, [toasts, removeToast]);

  const handleMouseEnter = (toastId) => {
    const toast = toasts.find((t) => t.id === toastId);
    if (toast?.variant) {
      const config = getVariantConfig(toast.variant);
      if (config.pauseOnHover) {
        // Pause animations if needed
        const elements = document.querySelectorAll(`[data-toast-id="${toastId}"] .animated-element`);
        elements.forEach(el => {
          const animation = anime.get(el);
          if (animation) animation.pause();
        });
      }
    }
  };

  const handleMouseLeave = (toastId) => {
    const toast = toasts.find((t) => t.id === toastId);
    if (toast?.variant) {
      const config = getVariantConfig(toast.variant);
      if (config.pauseOnHover) {
        // Resume animations
        const elements = document.querySelectorAll(`[data-toast-id="${toastId}"] .animated-element`);
        elements.forEach(el => {
          const animation = anime.get(el);
          if (animation) animation.play();
        });
      }
    }
  };

  return (
    <div
      style={{
        position: "fixed",
        zIndex: 9999,
        display: "flex",
        flexDirection: "column",
        gap: "10px",
        maxHeight: "100vh",
        overflowY: "auto",
        ...positionStyles[position],
      }}
    >
      {toasts.map((toast) => (
        <div
          key={toast.id}
          data-toast-id={toast.id}
          onMouseEnter={() => handleMouseEnter(toast.id)}
          onMouseLeave={() => handleMouseLeave(toast.id)}
          style={{
            opacity: 1,
            transform: "translateY(0)",
            transition: "all 0.3s ease",
          }}
        >
          {React.cloneElement(toast.component, {
            onClose: () => removeToast(toast.id),
            ...getVariantConfig(toast.variant, toast),
          })}
        </div>
      ))}
    </div>
  );
}
