import React, { useEffect, useRef } from "react";
import anime from "animejs";

const SmokeEffect = () => {
  return (
    <div className="smoke-container">
      <style>
        {`
          .smoke-container {
            position: absolute;
            inset: 0;
            overflow: hidden;
            pointer-events: none;
          }

          .smoke-container::before {
            content: '';
            position: absolute;
            inset: 0;
            background: radial-gradient(circle at center, transparent 30%, rgba(0,0,0,0.2));
            z-index: 1;
          }

          .smoke {
            position: absolute;
            width: 100%;
            height: 100%;
            filter: blur(10px) contrast(20);
          }

          .smoke::before,
          .smoke::after {
            content: '';
            position: absolute;
            width: 200%;
            height: 200%;
            background: #111;
            border-radius: 50%;
            transform-origin: -25% 50%;
          }

          .smoke::before {
            animation: smoke-right 5s linear infinite;
          }

          .smoke::after {
            animation: smoke-left 5s linear infinite;
            animation-delay: -2.5s;
          }

          @keyframes smoke-right {
            0% {
              transform: translateY(100%) rotate(0deg) scale(1);
              opacity: 0.5;
            }
            100% {
              transform: translateY(-50%) rotate(360deg) scale(0);
              opacity: 0;
            }
          }

          @keyframes smoke-left {
            0% {
              transform: translateY(100%) rotate(-180deg) scale(1);
              opacity: 0.5;
            }
            100% {
              transform: translateY(-50%) rotate(180deg) scale(0);
              opacity: 0;
            }
          }
        `}
      </style>
      <div className="smoke"></div>
    </div>
  );
};

const RevealText = ({ text }) => {
  return (
    <h1 style={{ margin: 0, padding: 0, fontSize: 'inherit', letterSpacing: '.2em' }}>
      {text.split('').map((char, i) => (
        <span
          key={i}
          style={{
            display: 'inline-block',
            opacity: 0,
            animation: 'animate 1s linear forwards',
            animationDelay: `${i * 0.1}s`,
          }}
        >
          {char}
        </span>
      ))}
      <style>
        {`
          @keyframes animate {
            0% {
              opacity: 0;
              transform: rotateY(90deg);
              filter: blur(10px);
            }
            100% {
              opacity: 1;
              transform: rotateY(0deg);
              filter: blur(0px);
            }
          }
        `}
      </style>
    </h1>
  );
};

export default function SmokeToast({
  message,
  style = {},
  description,
  action,
  onClose,
  ...rest
}) {
  const toastRef = useRef(null);
  const progressRef = useRef(null);
  const contentRef = useRef(null);

  useEffect(() => {
    // Initial entrance animation
    anime({
      targets: toastRef.current,
      translateY: [20, 0],
      opacity: [0, 1],
      duration: 800,
      easing: "easeOutExpo",
    });

    // Progress bar
    if (progressRef.current) {
      anime({
        targets: progressRef.current,
        width: ["0%", "100%"],
        duration: rest.duration || 5000,
        easing: "linear",
      });
    }
  }, []);

  return (
    <div
      ref={toastRef}
      style={{
        display: "flex",
        alignItems: "center",
        background: "linear-gradient(135deg, #1a1a1a, #2d2d2d)",
        color: "#FFFFFF",
        boxShadow: "0 8px 16px rgba(0, 0, 0, 0.2), 0 0 20px rgba(0, 0, 0, 0.1)",
        width: "320px",
        maxWidth: "90vw",
        minHeight: "60px",
        borderRadius: "12px",
        padding: "16px 24px",
        margin: "8px",
        fontSize: "16px",
        lineHeight: "1.5",
        position: "relative",
        overflow: "hidden",
        border: "1px solid rgba(255, 255, 255, 0.1)",
        backdropFilter: "blur(10px)",
        ...style,
      }}
      {...rest}
    >
      <SmokeEffect />
      <div
        style={{
          position: "absolute",
          bottom: 0,
          left: 0,
          right: 0,
          height: "3px",
          background: "rgba(255, 255, 255, 0.1)",
        }}
      >
        <div
          ref={progressRef}
          style={{
            height: "100%",
            background: "rgba(255, 255, 255, 0.3)",
            width: "0%",
          }}
        />
      </div>
      <div
        ref={contentRef}
        style={{
          flex: 1,
          position: "relative",
          zIndex: 1,
        }}
      >
        <RevealText text={message} />
        {description && (
          <div
            style={{
              fontSize: "14px",
              marginTop: "8px",
              opacity: 0.8,
              textShadow: "0 1px 2px rgba(0, 0, 0, 0.2)",
              animation: "fadeIn 0.5s ease forwards",
              animationDelay: "0.5s",
            }}
          >
            {description}
          </div>
        )}
      </div>
      {action && (
        <button
          onClick={action.onClick}
          style={{
            background: "rgba(255, 255, 255, 0.1)",
            border: "1px solid rgba(255, 255, 255, 0.2)",
            color: "inherit",
            padding: "8px 16px",
            marginLeft: "16px",
            cursor: "pointer",
            fontSize: "14px",
            fontWeight: 500,
            borderRadius: "6px",
            transition: "all 0.3s ease",
            position: "relative",
            zIndex: 1,
            backdropFilter: "blur(5px)",
          }}
          onMouseEnter={(e) => {
            e.target.style.background = "rgba(255, 255, 255, 0.2)";
            e.target.style.transform = "translateY(-1px)";
          }}
          onMouseLeave={(e) => {
            e.target.style.background = "rgba(255, 255, 255, 0.1)";
            e.target.style.transform = "translateY(0)";
          }}
        >
          {action.label}
        </button>
      )}
      <button
        onClick={onClose}
        style={{
          background: "transparent",
          border: "none",
          color: "white",
          fontSize: "18px",
          marginLeft: "12px",
          cursor: "pointer",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          width: "24px",
          height: "24px",
          padding: 0,
          position: "relative",
          zIndex: 2,
          opacity: 0.7,
          transition: "opacity 0.2s ease",
        }}
        onMouseEnter={(e) => {
          e.target.style.opacity = "1";
        }}
        onMouseLeave={(e) => {
          e.target.style.opacity = "0.7";
        }}
      >
        X
      </button>
    </div>
  );
}
